module.exports = function ($, Utils) {
    var ActionReg = function (type, source, dest, ok) {
        this.type = type;
        this.source = source;
        this.dest = dest;
        this.ok = ok;
        this.time = new Date().valueOf();
    };
    ActionReg.prototype = {
        constructor: ActionReg,
        type: null,
        source: null,
        dest: null,
        time: 0,
        isOk: false,
        $getXML: function () {
            var attr = {
                ok: this.ok,
                time: this.time
            };
            if (this.type)
                attr.type = this.type;
            if (this.source)
                attr.source = this.source;
            if (this.dest)
                attr.dest = this.dest;
            return $('<action/>', attr);
        },
        setProperties: function ($xml) {
            var actReg = this;
            $each($xml.get(0).attributes, function () {
                var name = this.name;
                var value = this.value;
                switch (name) {
                case 'type':
                case 'source':
                case 'dest':
                    actReg[name] = value;
                    break;
                case 'time':
                    actReg[name] = Number(value);
                    break;
                case 'ok':
                    actReg[name] = Utils.getBoolean(value, false);
                default:
                    break;
                }
            });
        },
        toHtmlString: function () {
            return '';
        }
    };
    return ActionReg;
}(require('jquery'), require('../Utils'));